/* underline effect */
.custom-menu .ant-menu-item,
.ant-menu > .ant-menu-submenu > .ant-menu-submenu-title,
.ant-menu-item-only-child {
  text-decoration: none !important;
  background-image: linear-gradient(to right, #fff, #fff),
    linear-gradient(to right, #c9c5c2, transparent),
    linear-gradient(to right, #c9c5c2, #c9c5c2) !important;
  background-size:
    0% 2px,
    100% 2px,
    0% 2px !important;
  background-position:
    0 100%,
    100% 100%,
    0 100% !important;
  background-repeat: no-repeat !important;
  transition: background-size 0.2s linear !important;
  /* background-color: red !important; */
  /* border: 2px solid red; */
  border-radius: 0 !important;
}
.custom-menu .ant-menu-item:hover,
.ant-menu > .ant-menu-submenu > .ant-menu-submenu-title:hover,
.ant-menu-item-only-child:hover {
  background-size:
    100% 2px,
    0% 2px,
    100% 2px !important;
  /* background-position: 0 100%, 100% 100%, 0 100%; */
}
.ant-menu > .ant-menu-item-selected {
  background-size:
    100% 2px,
    0% 2px,
    100% 2px !important;
    /* background-color: #61dafb !important; */
    background-color: transparent !important;

}

.custom-menu .ant-menu-item {
  width: auto; /* Allow width to be dynamic */
  /* min-width: 100px; 
  max-width: 200px; */
  margin: 0 3% 0 3%;
}
