.ant-menu-sub {
  background-color: rgba(0, 0, 0, 0.6) !important;
  /* font-family: 'UniNeueHeavyItalicr', cursive; */
  /* font-family: 'Alfa Slab One', display; */
  /* font-family: 'Bangers', display; */
}

/* submenu items */
.ant-menu-sub .ant-menu-title-content {
  color: #c9c5c2 !important;
  /* font-family: 'UniNeueHeavyItalic', cursive; */
  /* font-family: 'Alfa Slab One', display; */
  font-family: 'Bangers', display;
  font-size: 20px;
}
.ant-menu-sub .ant-menu-title-content:hover {
  color: #fff !important;
}

.ant-menu-submenu-title {
  display: flex !important;
  padding: 0 2vh;
  position: fixed !important;
  /* top: 0 !important; */
  /* right: 0 !important; */
}
.ant-menu-sub .ant-menu-submenu-title {
  padding: 0 2vh;
  position: sticky !important;
  /* background-color: blue !important; */
}

.ant-menu-submenu {
  background-color: transparent !important;
}

.ant-menu-item-group-title {
  color: #c9c5c2 !important;
}
.ant-menu-item-group-title:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.ant-layout-header
/* .ant-menu-horizontal, */
/* .custom-menu .ant-menu-item  */ {
  justify-content: center !important;
  height: 80px;
  line-height: 80px;
}

.layoutstyle {
  min-height: calc(var(--vh, 1vh) * 100) !important;
  min-width: calc(var(--vw, 1vw) * 100) !important;
  width: calc(var(--vw, 1vw) * 100) !important;
}

.headerstyle {
  min-width: calc(var(--vw, 1vw) * 100) !important;
  width: calc(var(--vw, 1vw) * 100) !important;
}

.colorpicker::-webkit-scrollbar {
  display: none;
}

.colorpicker {
  /* right: auto !important; */
  bottom: 0 !important;
  right: 0 !important;
  position: sticky;
  width: 48px !important;
  height: calc(var(--vh, 1vh) * 100 - 80px) !important;
  height: calc(100vh - 80px);
  overflow: scroll !important;
  /* background-color: #020305; */
}

.colorpicker > div {
  padding-left: 10px;
}

button {
  /* right: auto !important; */
  bottom: 0 !important;
  position: sticky;
  width: 48px;
  height: calc(var(--vh, 1vh) * 100 - 80px) !important;
  height: calc(100vh - 80px);
  text-wrap: wrap !important;
  cursor: pointer;
}

.mapdiv {
  margin: 80px 0 0 0 !important; 
}

/* Smaller screen menu height */
@media (max-width: 768px) {
  .ant-layout-header,
  .ant-menu-horizontal,
  .custom-menu .ant-menu-item,
  .ant-menu > .ant-menu-submenu > .ant-menu-submenu-title {
    height: 60px;
    line-height: 60px;
    font-size: 14px;
  }
  button, .colorpicker {
    height: calc(var(--vh, 1vh) * 100 - 60px) !important;
    height: calc(100vh - 60px);
  }
  .ant-menu-sub .ant-menu-title-content {
    font-size: 12px;
  }
  .custom-menu .ant-menu-item {
    min-width: 40px; /* Set a minimum width */
    max-width: 140px; /* Set a maximum width */
    margin: 0 1% 0 1%;
  }
  .ant-menu-submenu-title {
    /* top: 0 !important; */
    right: 0 !important;
  }
  .custom-menu .ant-menu-item.home {
    left: 0 !important;
    position: fixed !important;
  }
  .mapdiv {
    margin: 60px 0 0 0 !important; 
  }
  .ant-layout-header {
    /* margin-left: auto; */
    /* left: auto; */
    justify-content: space-evenly !important;
    /* background-color: #020305; */
  }
}

